import React, { useState, Fragment } from "react";
import { Image } from "semantic-ui-react";
import Particles from "react-particles-js";
import "./style.css";
import validator from "validator";
import { CircularProgress } from "@material-ui/core";
import url from "../../../strings/url";
const EnterEmail = () => {
  const [email, setEmail] = useState("");
  const [loading, setLoading] = useState(false);
  const [status, setStatus] = useState("");

  const handleSendEmail = () => {
    setLoading(true);
    const headers = new Headers();
    headers.append("content-type", "application/json");
    fetch(`${url}/pass_change/${email}`, { method: "get", headers })
      .then(async (res) => {
        if (res.ok) {
          setStatus("success");
          return setLoading(false);
        }
        if (res.status === 404) {
          setStatus("NotFound");
          return setLoading(false);
        }
        setStatus("internal");
        setLoading(false);
      })
      .catch((e) => {
        setStatus("internal");
        setLoading(false);
      });
  };

  return (
    <div
      style={{
        height: "100vh",
        width: "100%",
        background: "black",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        flexDirection: "column",
      }}
    >
      <Particles
        style={{ position: "fixed", zIndex: 1, left: 0, top: 0 }}
        params={{
          particles: {
            number: {
              value: 30,
            },
            size: { value: 5 },
          },
        }}
      ></Particles>
      <div
        className={status === "NotFound" ? "rotate" : ""}
        style={{
          zIndex: 3,
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          flexDirection: "column",
          position: "relative",
          width: 320,
          height: 400,
          background: "black",
          transition: "all ease-in-out 1s",
          borderRadius: 10,
          boxShadow: "0px 0px 18px rgba(255,255,255,0.4)",
        }}
      >
        <Image
          src={require("../../../images/complainify.png")}
          size="small"
          style={{ marginBottom: 30 }}
        ></Image>
        {!loading && status === "" ? (
          <Fragment>
            <input
              placeholder="Enter The Email"
              onChange={(e) => {
                setEmail(e.target.value);
              }}
              value={email}
              className="enterEmail comingFromLeftP5"
            ></input>
            <button
              style={{ marginTop: 60 }}
              className="passResetButton comingFromLeftP5"
              disabled={!validator.isEmail(email)}
              onClick={() => {
                handleSendEmail();
              }}
            >
              Forgot Password
            </button>
          </Fragment>
        ) : (
          ""
        )}
        {loading ? <CP></CP> : ""}
        {!loading && status === "success" ? <Success></Success> : ""}
        {!loading && status === "NotFound" ? (
          <NotFound
            handleAnother={() => {
              setStatus("");
            }}
          ></NotFound>
        ) : (
          ""
        )}
        {!loading && status === "internal" ? (
          <NotFound
            internal={true}
            handleAnother={() => {
              setStatus("");
            }}
          ></NotFound>
        ) : (
          ""
        )}
      </div>
    </div>
  );
};

const CP = ({ style }) => {
  return (
    <CircularProgress
      style={{
        color: "rgba(255,255,255,0.6)",
        height: 20,
        width: 20,
        marginTop: 10,
        ...style,
      }}
    ></CircularProgress>
  );
};
const Success = (props) => {
  return (
    <div
      style={{
        fontFamily: "poppins",
        fontWeight: "bold",
        color: "white",
        marginBottom: 10,
        fontSize: 12,
      }}
    >
      Email Sent Successfully Check out your Email
    </div>
  );
};

const NotFound = ({ handleAnother, internal }) => {
  return (
    <Fragment>
      <div
        style={{
          fontFamily: "poppins",
          fontWeight: "bold",
          color: "white",
          marginBottom: 10,
          fontSize: 12,
        }}
      >
        {internal
          ? "Three Might Be Some Internal Error Pardon !!"
          : "Email you Entered is Not Linked With Anonymity..."}
      </div>
      <button
        style={{ marginTop: 60 }}
        className="passResetButton"
        onClick={() => {
          handleAnother();
        }}
      >
        {internal ? "Try Again" : "Try Another One"}
      </button>
    </Fragment>
  );
};
export default EnterEmail;
